.modal {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.modal .formulario {
  max-width: 60rem;
  max-height: 90%;
  background-color: #FFFFFF;
  padding: 2rem;
  border-radius: 1rem;
  margin: 0 auto;
  transform: translateY(-30rem);
  transition-property: all;
  transition-timing-function: ease-in-out;
  transition-duration: 300ms;
  opacity: 0;
  overflow-x: scroll;
}
.modal .formulario legend {
  font-size: 2rem;
  text-align: center;
  font-weight: bold;
  margin-bottom: 2rem;
  width: 100%;
}
.modal .formulario .opciones {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  align-items: center;
  padding-top: 2rem;
}
.modal .formulario .opciones .registro-nuevo {
  background-color: #10439F;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  border-radius: 1rem;
  display: inline-block;
}
.modal .formulario .opciones .registro-nuevo:hover {
  background-color: rgb(11.3371428571, 47.4742857143, 112.6628571429);
  cursor: pointer;
}
.modal .formulario .opciones .cerrar-modal {
  background-color: #FF5B00;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  border-radius: 1rem;
  display: inline-block;
}
.modal .formulario .opciones .cerrar-modal:hover {
  background-color: rgb(204, 72.8, 0);
  cursor: pointer;
}
.modal .formulario .opciones input, .modal .formulario .opciones button {
  margin: 0;
  padding: 1rem;
}
.modal .formulario.animar {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
.modal .formulario.cerrar {
  transform: translateY(-200%);
  opacity: 0;
}